'use client';

import React from 'react';
import { useQuery } from '@tanstack/react-query';
import {
  Paper,
  Stack,
  TextField,
  MenuItem,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  Typography,
} from '@mui/material';

const SPORTS = ['Football', 'Basketball', 'Baseball', 'Softball', 'Soccer', 'Volleyball', 'Lacrosse'];

function useRecruits(sport: string) {
  return useQuery({
    queryKey: ['on3-recruits', sport],
    queryFn: async () => {
      const res = await fetch(`/api/recruits?sport=${encodeURIComponent(sport)}`);
      if (!res.ok) throw new Error('Failed to load recruits');
      const json = await res.json();
      return json.data || [];
    },
  });
}

export function RecruitsTable() {
  const [sport, setSport] = React.useState('Football');
  const q = useRecruits(sport);

  return (
    <Paper variant="outlined" sx={{ p: 2, borderRadius: 2 }}>
      <Stack direction="row" spacing={2} alignItems="center" sx={{ mb: 2 }}>
        <Typography variant="h6">On3 Rivals Top 20</Typography>
        <TextField
          select
          size="small"
          label="Sport"
          value={sport}
          onChange={(e) => setSport(e.target.value)}
          data-testid="on3-sport-select"
        >
          {SPORTS.map((s) => (
            <MenuItem key={s} value={s}>
              {s}
            </MenuItem>
          ))}
        </TextField>
      </Stack>
      <Table size="small" stickyHeader data-testid="on3-recruits-table">
        <TableHead>
          <TableRow>
            <TableCell>Name</TableCell>
            <TableCell>Pos</TableCell>
            <TableCell>Class</TableCell>
            <TableCell>Hometown</TableCell>
            <TableCell>High School</TableCell>
            <TableCell>Rating</TableCell>
            <TableCell>Ranking</TableCell>
            <TableCell>Committed</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {(q.data || []).map((r: any) => (
            <TableRow key={r.id}>
              <TableCell>{r.name}</TableCell>
              <TableCell>{r.position || '-'}</TableCell>
              <TableCell>{r.classYear || '-'}</TableCell>
              <TableCell>{r.hometown || '-'}</TableCell>
              <TableCell>{r.highSchool || '-'}</TableCell>
              <TableCell>{r.rating || '-'}</TableCell>
              <TableCell>{r.ranking || '-'}</TableCell>
              <TableCell>{r.committedTo || '-'}</TableCell>
            </TableRow>
          ))}
          {!q.isLoading && (!q.data || q.data.length === 0) && (
            <TableRow>
              <TableCell colSpan={8} align="center">
                No results
              </TableCell>
            </TableRow>
          )}
        </TableBody>
      </Table>
    </Paper>
  );
}


